.ctpc_card {
  width: 100%;
  min-height: 50px;
  height: fit-content;
  border-radius: 12px;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
  background: #ffffff;
  box-shadow: 2px 4px 32px 0px rgba(0, 0, 0, 0.04);
  margin-top: 1rem;
}
.ctpcc_conatiner {
  width: 100%;
  height: 50px;
  background-color: #32478d;
  border: none;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  filter: drop-shadow(2px 4px 32px rgba(0, 0, 0, 0.04));
  padding-left: 1rem;
  padding-right: 1rem;
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  align-items: center;
  cursor: pointer;
  position: relative;
}

.ctpcc_conatiner > h6 {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #ffffff;
  margin: 0;
}
.ctpcc_conatiner > p,
.ctpc_card > p {
  font-weight: 400;
  font-size: 0.878vw;
  line-height: 1.098vw;
  color: rgba(0, 0, 0, 0.6);
  margin-bottom: 0;
}
.ctlc_card {
  width: 100%;
  padding-inline: 1rem;
  height: fit-content;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.7rem;
  border-bottom: 0.5px solid #cacaca;
}
.ctlcc_container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.ctlcc_container > h6 {
  font-weight: 400;
  font-size: 14px;
  margin: 0;
  color: #121212;
}

.ctlcc_container > p {
  font-weight: 400;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.6);
  margin: 0;
}
